                      v-text="item.value.contractPeriod===-1 ? '' : (item.value.contractPeriod%12===0 ? item.value.contractPeriod/12+'' : (item.value.contractPeriod%6===0 ? '' : item.value.contractPeriod+''))"></span>
                  </el-form-item>
                  <el-form-item v-if="item.value.contracType === 0" label="">
