    <div id="imageScale-wrapper">
      <mt-popup
        v-model="imageScale"
        class="imageScale-wrapper"
        closeOnClickModal="false">
        <div class="imageScale-box">
          <img :src="imageScaleUrl" @click="imageScaleClose">
        </div>
      </mt-popup>
