<template>
  <div id="signIn-wrapper">
    <!---->
    <div class="signIn-header">
      <div class="signIn-header-left">
        <img :src="imgSrc.header">
      </div>
      <div class="signIn-header-center">
        <p v-text="objNr.fetchPostion+' '+objNr.fetchName"></p>
      </div>
      <div class="signIn-header-right">
        <p v-text="year+''+month+''+data+''"></p>
      </div>
    </div>
    <!---->
    <div class="signIn-article">
      <div class="signIn-article-left">
        <div v-if="showtwStstus" class="signIn-article-line"></div>
        <!--<div class="signIn-article-line"></div>-->
        <p class="article-icon-up"></p>
        <p v-if="showtwStstus" class="article-icon-down"></p>
        <!--<p class="article-icon-down"></p>-->
      </div>
      <div class="signIn-article-right" v-if="showtwStstus">
        <div class="signIn-article-top">
          <p>
            <span v-text="' '+initToTime"></span>
            <span class="article-tab article-tab-kg" v-if="absenteeismStatus"></span>
            <span class="article-tab article-tab-zc" v-if="initDaKaRecord"></span>
            <span class="article-tab article-tab-cd" v-if="lateStatus"></span>
            <span class="article-tab article-tab-qyw" v-if="toOutArea"></span>
            <!--<span class="article-tab article-tab-zt"></span>-->
            <!--<span class="article-tab article-tab-sq"></span>-->
          </p>
          <p>
            <img :src="imgSrc.PostionIcon">
            <span v-text="': '+ ((!toDaKaStatusIsOutsideInit) ? '' : (twRangeShow+''))"></span>
          </p>
          <p>
            <span class="article-tab article-tab-sq" v-if="toUpAbsenteeismStatus"
                  @click="submitApplyRouter(0)">/</span>
            <span class="article-tab article-tab-wdk" v-if="toUpForgetPunch" @click="submitApplyRouter(1)"></span>
          </p>
        </div>
        <div class="signIn-article-bottom" v-if="zcDownShowSpan">
          <p>
            <span v-text="' '+goToTime"></span>
            <span class="article-tab article-tab-kg" v-if="toDownKuang"></span>
            <span class="article-tab article-tab-zc" v-if="initDownRecord"></span>
            <span class="article-tab article-tab-zt" v-if="leaveEarly"></span>
            <!--<span class="article-tab article-tab-cd"></span>-->
            <!--<span class="article-tab article-tab-sq"></span>-->
            <span class="article-tab article-tab-qyw" v-if="downOutArea"></span>
          </p>
          <p>
            <img :src="imgSrc.PostionIcon">
            <span v-text="': '+((!toDownKaStatusIsOutsideInit) ? '' : (owRangeShow+''))"></span>
          </p>
          <p>
            <span class="article-tab article-tab-sq" v-if="toDownAbsenteeismStatus"
                  @click="submitApplyRouter(0)">/</span>
            <span class="article-tab article-tab-jbsq" @click="submitApplyRouter(3)"
                  v-if="toDownAddTimeStatus"></span>
          </p>
        </div>
      </div>
    </div>
    <!---->
    <div class="Punch-btn-wrapper" v-if="daKaHide">
      <div class="Punch-btn-bg">
        <img :src="(toClickSpan || downClickSpan) ? imgSrc.bg : imgSrc.bg1">
      </div>
      <mt-button class="Punch-btn-btn" type="primary" :disabled="!(toClickSpan || downClickSpan)"
                 @click="handerClickEvent">
        <p v-text="toClickSpan ? '' : (downClickSpan ? '' : '')"></p>
        <p v-text="(toClickSpan || downClickSpan) ? (hour + ':'+ minute + ':'+second) : ''"></p>
      </mt-button>
    </div>
    <!---->
    <mt-popup
      v-model="qulocation"
      class="getLocation-alert-wrapper"
      closeOnClickModal="false">
      <div class="getLocation-alert-content" v-if="">
        <p v-if="searchLocationArray.length && !failModel && !wifiPopup">HR SAAS</p>
        <p v-if="!searchLocationArray.length && !failModel && !wifiPopup"></p>
        <p v-if="failModel && !wifiPopup" v-text="failModelErr ? '' : ''"></p>
        <p v-if="wifiPopup">wifi</p>
      </div>
      <div class="getLocation-alert-btnBox">
        <p @click="closeAlert" class="getLocation-alert-btn"
           :class="(searchLocationArray.length && !failModel) ? 'getLocation-alert-btnLeft' :  'getLocation-alert-btnColor getLocation-alert-btnCenter'"
           v-text="(searchLocationArray.length && !failModel) ? '' : ''">
        </p>
        <p v-if="searchLocationArray.length && !failModel && !wifiPopup" @click="okClickEvent"
           class="getLocation-alert-btn getLocation-alert-btnRight">
          
        </p>
        <p v-if="wifiPopup" @click="okClickWifi"
           class="getLocation-alert-btn getLocation-alert-btnRight">
          
        </p>
      </div>
    </mt-popup>
    <!---->
    <mt-popup
      v-model="popupVisible"
      class="punch-success-wrapper"
      closeOnClickModal="false"
    >
      <div class="punch-success-box">
        <div class="punch-success-header">
          <div class="punch-success-headerLeft"></div>
          <p class="punch-success-headerCenter"></p>
          <div class="punch-success-headerRight"></div>
        </div>
        <div class="punch-success-content">
          <div class="punch-success-img">
            <img :src="imgSrc.alertHeader">
          </div>
          <p class="punch-success-time" v-if="alertToSpan" v-text="' '+' '+initToTime"></p>
          <p class="punch-success-time" v-if="alertDownSpan" v-text="' '+' '+goToTime"></p>
          <div class="punch-success-tab">
            <mt-button type="default"
                       class="punch-success-tabHeight article-tab-zc"
                       v-if="initDownRecord">
              <span></span>
            </mt-button>
            <mt-button type="default"
                       class="punch-success-tabHeight article-tab-cd"
                       v-if="lateState">
              <span></span>
            </mt-button>
            <mt-button type="default"
                       class="punch-success-tabHeight article-tab-kg"
                       v-if="absenteeismStatusAlert">
              <span></span>
            </mt-button>
            <mt-button type="default"
                       class="punch-success-tabHeight article-tab-zt"
                       v-if="leaveEarly">
              <span></span>
            </mt-button>
            <mt-button type="default"
                       class="punch-success-tabHeight article-tab-qyw"
                       v-if="isYellowAddQ">
              <span></span>
            </mt-button>
          </div>
        </div>
        <div class="punch-success-btn" @click="knowFunction">
          <span></span>
        </div>
      </div>
    </mt-popup>
  </div>
</template>
<script>

  import {MessageBox, Popup} from 'mint-ui';
  import moment from 'moment'

  let df = 'HH:mm:ss';
  let timer1 = null;
  export default {
    data(){
      return {
        objNr: {},// 
        failModel: false,// 
        failModelErr: false,// 
        alertNoDaka: '',
        year: new Date().getFullYear(), //
        month: new Date().getMonth() + 1, //
        data: new Date().getDate(), //
        imgSrc: {
          header: require('../../assets/tx.png'), // 
          PostionIcon: require('../../assets/ico_location_1.png'), // 
          bg: require('../../assets/0_gif.gif'), // 1
          bg1: require('../../assets/0_gif1.gif'), // 2
          alertHeader: require('../../assets/pic_check in.png'), // 
        },
        initToTime: '', // 
        goToTime: '', // 
        alertToSpan: false, //
        alertDownSpan: false, //
        popupVisible: false,//
        outsideObtainValue: true, //truefalse
        searchLocationArray: [],  //locations
        twRange: '', //
        twRangeShow: '',// 
        owRangeShow: '',// 
        daKaHide: true,//
        toClickSpan: true, //
        getLocations: false, // 
        downClickSpan: false, //
        zcDownShowSpan: false, //
        toDownAbsenteeismStatus: false, //
        toUpAbsenteeismStatus: false, //
        toUpForgetPunch: false,//
        showOwStstusX: false, //
        showtwStstus: false,//
        toOutArea: false, //
        downOutArea: false, //
        absenteeismStatus: false, // 
        absenteeismStatusAlert: false, //
        lateStatus: false, //
        lateState: false, // 
        isYellowAddQ: false,  //
        initDaKaRecord: false, //
        leaveEarly: false,  //
        toDownKuang: false,  //
        toDaKaStatusIsInit: '', //
        toDaKaStatusIsOutsideInit: false, //
        toDownKaStatusIsInit: '', //
        toDownKaStatusIsOutsideInit: false, //
        toDaKaStatusIs: '', //
        toDaKaStatusIsOutside: false, //
        toDownKaStatusIs: '', //
        toDownKaStatusIsOutside: false, //
        initDownRecord: false,//
        toDownAddTimeStatus: false, //
        qulocation: false, //
        hour: 0,// 
        minute: 0,// 
        second: 0,// 
        wifiPopup: false, // wifi
        isWifi: false, // wifi
        wifiIP: '',// wifiIP
      }
    },
    created: function () {
      this.doSearch(); //
    },
    methods: {
      //
      doSearch(){
        this.handerSign();
        setInterval(this.handerSign, 1000);
        var imageString = this.getCookie('avatarImages'); //
        this.objNr = {
          fetchPostion: this.getCookie('infoObjPassPostion'),
          fetchName: this.getCookie('infoObjPassName'),
        };
        if (imageString) {
          this.imgSrc.header = imageString;
        }
        this.$http.post('/api/v1.0/client/findPunchCardLog').then(response => { //
          if (response.body.code === 200) {
            if (response.body.result) {
              //start
              this.searchLocationArray = response.body.result.locations;
              this.isWifi = response.body.result.isWifi;
              //end
              //start
              this.twRangeShow = response.body.result.twLocation;
              this.owRangeShow = response.body.result.owLocation;
              //end
              //falsestart
              this.fanwei = response.body.result.twOutside;
              this.fanweixia = response.body.result.owOutside;
              this.toDaKaStatusIsInit = response.body.result.twStatus; //
              this.toDaKaStatusIsOutsideInit = response.body.result.twOutside; //
              this.toDownKaStatusIsInit = response.body.result.owStatus;//
              this.toDownKaStatusIsOutsideInit = response.body.result.owOutside; //
              if (response.body.result.twTime) {
                this.toClickSpan = false;
                this.downClickSpan = true;
              }
              if (response.body.result.twTime) {
                this.initToTime = moment(response.body.result.twTime).format(df);
              }
              if (response.body.result.owTime) {
                this.goToTime = moment(response.body.result.owTime).format(df);
              }
              //
              //   ******************************************
              if (this.PunchClock(this.toDaKaStatusIsInit)) {  // 
                this.showtwStstus = true;
                this.toUpForgetPunch = false; // 
//                this.zcDownShowSpan = true; //
                this.toOutArea = false; // 
                this.absenteeismStatus = false;//
                this.toUpAbsenteeismStatus = false;
                this.lateStatus = false;
                this.initDaKaRecord = false;
                if (this.toDaKaStatusIsOutsideInit) { //
                  this.toOutArea = true;
                  this.toUpAbsenteeismStatus = true;
                }
                if (this.toDaKaStatusIsInit === 0) {//
                  this.initDaKaRecord = true;
                } else if (this.toDaKaStatusIsInit === 1) { //
                  this.lateStatus = true; //
                  this.toUpForgetPunch = true; //
                } else if (this.toDaKaStatusIsInit === 2) { //
                  this.absenteeismStatus = true;
                  this.toUpForgetPunch = true;//
                }
              }
              if (this.PunchClock(this.toDownKaStatusIsInit)) {     //****
                this.zcDownShowSpan = true;
                this.downOutArea = false;
                this.toDownAbsenteeismStatus = false;
                this.leaveEarly = false;// 
                this.initDownRecord = false;
                this.toDownAddTimeStatus = false;
                this.daKaHide = false;  //
                if (this.toDownKaStatusIsOutsideInit) { //
                  this.downOutArea = true;
                  this.toDownAbsenteeismStatus = true;
                }
                if (this.toDownKaStatusIsInit === 0 || this.toDownKaStatusIsInit === 2) {//
                  if (this.toDownKaStatusIsInit === 2) { //
                    this.toDownAddTimeStatus = true;
                  }
                  this.initDownRecord = true;
                } else if (this.toDownKaStatusIsInit === 1) { //
                  this.leaveEarly = true; //
                  this.toDownAbsenteeismStatus = true;
                } else if (this.toDownKaStatusIsInit === 3) { //
                  this.toDownKuang = true;
                  this.toDownAbsenteeismStatus = true;
                }
              }
            }
          } else if (response.body.code === 500) { //
            MessageBox('', response.body.message);
            this.daKaHide = false;
            if (response.body.message === '') {
              this.$router.push({path: '/signCard'});
            }
          }
        }, response => {
//          console.log('error callback');
        });
      },
      //
      // 
      handerSign(){
        let oDate = new Date();
        this.hour = AddZero(oDate.getHours());
        this.minute = AddZero(oDate.getMinutes());
        this.second = AddZero(oDate.getSeconds());
        function AddZero(n) {
          if (n < 10) {
            return '0' + n;
          }
          return '' + n;
        }
      },
      // 
      PunchClock(state){
        return typeof(state) === 'number' && state >= 0;
      },
      handerClickEvent(){  //   
        if (navigator.onLine) { //
          if (this.isWifi) {
            this.wifiPopup = true;// wifi
          }
          this.wifiIP = '';
          this.qulocation = true;
        } else { //
          MessageBox('', '');
        }
      },
      closeAlert(){ //alert
        this.qulocation = false;
        // 
        setTimeout(() => {
          this.failModel = false;
          this.wifiPopup = false;
        }, 300);

      },
      knowFunction(){
        this.popupVisible = false;
        this.qulocation = false;
      },
      //
      submitApplyRouter(type){
        this.setCookie('leaveType', type, 365);
        this.$router.push({path: '/leave'});
      },
      //wifi
      okClickWifi(){
        this.wifiPopup = false;
        this.qulocation = true;
      },
      // 
      okClickEvent(){
        let downClickSpan = this.downClickSpan;
        let toClickSpan = this.toClickSpan;
        this.qulocation = false;
        this.downClickSpan = false;
        this.toClickSpan = false;
        this.getLocations = true;
        let BMap = null;
        let map = null;
        let self = this;
        let curl;
        //
        let u = navigator.userAgent;
        let isAndroid = '0';
        isAndroid = u.indexOf('Android') > -1 || u.indexOf('Adr') > -1; //android
        if (isAndroid) {
          curl = {
            location: location.href //
          };
        }
        if (!isAndroid) {
          curl = {
            location: location.href.toString().split('#')[0] //
          };
        }
        //
        new Promise((resolve, reject) => {
          window._initBaiduMap = function () {
            window.document.body.removeChild($script);
            window._initBaiduMap = null;
            resolve();
            BMap = window.BMap;
            map = new BMap.Map();
          };
          // 
          const $script = document.createElement('script');
          window.document.body.appendChild($script);
          $script.src = `//api.map.baidu.com/api?v=2.0&ak=FRMO4GzB3wRlgFrAURcQSKWdZmzHuuD4&callback=_initBaiduMap`;
          // ip
          const $scripts = document.createElement('script');
          window.document.body.appendChild($scripts);
          $scripts.src = "http://pv.sohu.com/cityjson?ie=utf-8";
        }).then(() => {
          this.$http.post('/api/v1.0/wechat/sign', curl).then(response => { //
            if (response.body.code === 200) {
              this.t1 = response.body.result.appid.toString();
              this.t2 = response.body.result.timestamp.toString();
              this.t3 = response.body.result.nonceStr.toString();
              this.t4 = response.body.result.signature.toString();
              this.yyy = true;
              let cvt = new BMap.Convertor();
              wx.config({
                debug: false,
                appId: this.t1,
                timestamp: this.t2,
                nonceStr: this.t3,
                signature: this.t4,
                jsApiList: [
                  'getLocation'
                ]
              });
              wx.error(function (res) {
                // 
                self.failModelErr = true;
                self.failModel = true;
                self.qulocation = true;
                self.downClickSpan = downClickSpan;
                self.toClickSpan = toClickSpan;
                self.getLocations = false;
              });
              wx.ready(function () {
                wx.getLocation({
                  type: 'wgs84',
                  success: function (res) {
                    self.getLocations = false;
                    self.latitude = res.latitude; // 90 ~ -90           res.latitude;
                    self.longitude = res.longitude; // 180 ~ -180     res.longitude;  //
                    var speed = res.speed; // /
                    var accuracy = res.accuracy; // 
                    cvt.translate([new BMap.Point(res.longitude, res.latitude)]/*  wx */, 1, 5, (data) => {   //
                      if (data.status === 0) {
                        let myPosition = data.points[0]; // 
                        self.$http.post('/api/v1.0/client/findPunchCardLog').then(response => { //
                          if (response.body.code === 200) {
                            //start
                            let scopes = [];
                            self.searchLocationArray = (rt => {
                              if (!rt) return false;
                              let out = [];
                              rt.forEach(item => {
                                out.push(new BMap.Point(item.LONGITUDE, item.LATITUDE));
                                scopes.push(item.SCOPE);
                              });
                              return out;
                            })(response.body.result.locations);

                            let arrayLonglat = self.searchLocationArray;
                            cvt.translate(arrayLonglat/*  */, 3, 5, (data) => {
                              if (data.status === 0) {
                                // 
                                let distance;
                                self.twRange = '';
                                self.outsideObtainValue = true;// true
                                for (let i = 0; i < data.points.length; i++) {
                                  distance = map.getDistance(data.points[i], myPosition);
                                  if (distance < scopes[i]) {
                                    self.outsideObtainValue = false;
                                    break;
                                  }
                                }
                                if (self.outsideObtainValue) {
                                  new BMap.Geocoder().getLocation(myPosition, function (res) { //
                                    self.twRange = res.addressComponents.district + res.addressComponents.street;
                                    if (!self.twRange) {
                                      self.twRange = '';
                                    }
                                    self.punchInfo(self, self.twRange);
                                  });
                                } else {
                                  self.punchInfo(self, self.twRange);
                                }
                                // 
                                if (self.PunchClock(self.toDaKaStatusIsInit)) {
                                  self.daKaHide = false;
                                }
                              } else {
                                self.downClickSpan = downClickSpan;
                                self.toClickSpan = toClickSpan;
                                self.getLocations = false;
                                MessageBox('', '');
                              }
                            });
                          } else if (response.body.code === 500) {
                            MessageBox('', response.body.message);
                            self.daKaHide = false;
                          }
                        }, response => {
//                        console.log('error callback');
                        });
                      } else {
                        self.downClickSpan = downClickSpan;
                        self.toClickSpan = toClickSpan;
                        self.getLocations = false;
                        MessageBox('', '');
                      }
                    });
                  },
                  cancel: function (res) {
                    //
                    let u = navigator.userAgent;
                    let isAndroid = '0';
                    isAndroid = u.indexOf('Android') > -1 || u.indexOf('Adr') > -1; //android
                    self.downClickSpan = downClickSpan;
                    self.toClickSpan = toClickSpan;
                    self.getLocations = false;
                    if (isAndroid) {
                      MessageBox('', '');
                    }
                    if (!isAndroid) {
                      MessageBox('', '');
                    }
                    //
                  },
                  fail: function (res) {
                    // 
                    self.failModelErr = false;
                    self.failModel = true;
                    self.qulocation = true;
                    self.downClickSpan = downClickSpan;
                    self.toClickSpan = toClickSpan;
                    self.getLocations = false;
                  }
                });
              });
            } else {
              this.downClickSpan = downClickSpan;
              this.toClickSpan = toClickSpan;
              this.getLocations = false;
              MessageBox('', response.body.message);
            }
          }, response => {
            console.log('error callback');
          });
        });
        //
      },
      // ip
      getIP(){
        if (returnCitySN["cip"]) {
          this.wifiIP = returnCitySN["cip"];
        } else {
          this.getIP();
        }
      },
      punchInfo(self, twRange){
        // ip
        if (this.isWifi) {
          this.getIP();
        }
        let updakaObj;
        if (!self.PunchClock(self.toDaKaStatusIsInit)) {
          updakaObj = {
            record: {
              twOutside: self.outsideObtainValue ? true : 0,
              twLocation: twRange,
              wifi: self.wifiIP
            }
          }
        } else if (self.PunchClock(self.toDaKaStatusIsInit) && !self.PunchClock(self.toDownKaStatusIsInit)) {
          updakaObj = {
            record: {
              owOutside: self.outsideObtainValue ? true : 0,
              owLocation: twRange,
              wifi: self.wifiIP
            }
          }
        }

        self.$http.post('/api/v1.0/client/punchCardLog', updakaObj).then(response => { //
          if (response.body.code === 200) {
            self.toDaKaStatusIs = response.body.result.twStatus; //
            self.toDaKaStatusIsOutside = response.body.result.twOutside; //
            self.toDownKaStatusIs = response.body.result.owStatus;//
            self.toDownKaStatusIsOutside = response.body.result.owOutside; //
            if (!self.PunchClock(self.toDaKaStatusIsInit)) {//
              self.popupVisible = true;
              self.alertToSpan = true;//sapn 
              self.alertDownSpan = false; //sapn 
              self.absenteeismStatusAlert = false; // 
//              self.lateStatus = false;
              self.lateState = false;
              self.isYellowAddQ = false; // 
              self.downClickSpan = true; //  
              if (response.body.result.twTime) {
                self.initToTime = moment(response.body.result.twTime).format(df);
              }
              if (self.toDaKaStatusIsOutside) { //   
                self.isYellowAddQ = true; //alert
              }
              if (self.toDaKaStatusIs === 0) {//
                self.initDownRecord = true;
              } else if (self.toDaKaStatusIs === 1) { //
                self.lateState = true;
              } else if (self.toDaKaStatusIs === 2) { //
                self.absenteeismStatusAlert = true;
              }

            } else if (self.PunchClock(self.toDaKaStatusIsInit) && !self.PunchClock(self.toDownKaStatusIsInit)) { //
              //   
              self.alertToSpan = false;//sapn 
              self.alertDownSpan = true; //sapn 
              self.popupVisible = true; //
              self.absenteeismStatusAlert = false;
              self.lateState = false;
              self.toDownAbsenteeismStatus = false;
              self.isYellowAddQ = false; // 
              self.leaveEarly = false; // 
              self.initDownRecord = false;
              if (response.body.result.owTime) {
                self.goToTime = moment(response.body.result.owTime).format(df);
              }
              if (self.toDownKaStatusIsOutside) { //
                self.isYellowAddQ = true;
                self.toDownAbsenteeismStatus = true; //
              }
              if (self.toDownKaStatusIs === 0) {//
                self.initDownRecord = true;
              } else if (self.toDownKaStatusIs === 1) { //
                self.leaveEarly = true;
              } else if (self.toDownKaStatusIs === 3) { //
                self.toDownAbsenteeismStatus = true;
                self.absenteeismStatusAlert = true;
              }
            }
            self.doSearch();
          } else if (response.body.code === 500) {
            MessageBox('', response.body.message);
            self.daKaHide = false;
          }
        }, response => {
        });
      }
    },
  }

</script>

<style lang="scss">
  #signIn-wrapper {
    position: relative;
    width: 100%;
    height: 100%;
    background-color: #ffffff;
    .clearFix {
      &:before {
        content: "";
        display: table
      }
      &:after {
        content: "";
        display: table;
        clear: both;
        overflow: hidden;
        *zoom: 1
      }
    }
    p {
      margin: 0;
    }
    .signIn-header {
      position: relative;
      box-sizing: border-box;
      overflow: hidden;
      width: 100%;
      padding: 10px 15px 10px 60px;
      background: #20a1ff;
      font-size: 14px;
      color: #ffffff;
      .signIn-header-left {
        float: left;
        box-sizing: border-box;
        margin-left: -45px;
        width: 45px;
        height: 45px;
        border-radius: 50%;
        img {
          width: 100%;
          height: 100%;
          border-radius: 50%;
        }
      }
      .signIn-header-center {
        float: left;
        box-sizing: border-box;
        margin-top: 15px;
        padding-left: 10px;
        padding-right: 170px;
        width: 100%;
        line-height: 20px;
        text-align: left;
      }
      .signIn-header-right {
        position: absolute;
        top: 16px;
        right: 15px;
        padding: 0 10px;
        height: 30px;
        line-height: 30px;
        text-align: center;
        border: 1px solid #ffffff;
        border-radius: 4px;
        background-color: #4db3ff;
      }
    }
    .signIn-article {
      position: relative;
      box-sizing: border-box;
      width: 100%;
      padding: 25px 20px;
      .signIn-article-left {
        position: relative;
        width: 22px;
        height: 144px;
        p {
          position: absolute;
          width: 22px;
          height: 22px;
          border-radius: 50%;

        }
        .article-icon-up {
          top: 0;
          background: url("../../assets/ico_to.png");
          background-size: 22px 22px;
        }
        .article-icon-down {
          bottom: 0;
          background: url("../../assets/ico_off.png");
          background-size: 22px 22px;
        }
        .signIn-article-line {
          position: absolute;
          top: 1px;
          left: 10px;
          height: 142px;
          border-left: 1px solid #98abbf;
        }
      }
      .signIn-article-right {
        position: absolute;
        top: 25px;
        text-align: left;
        box-sizing: border-box;
        padding-left: 22px;
        width: 90%;
        .signIn-article-top, .signIn-article-bottom {
          box-sizing: border-box;
          padding-left: 10px;
          font-size: 0;
          p {
            box-sizing: border-box;
            width: 100%;
            min-height: 22px;
            margin-bottom: 4px;
            span {
              box-sizing: border-box;
              display: inline-block;
              min-height: 22px;
              font-size: 14px;
              margin-right: 5px;
              text-align: center;
              border-radius: 4px;
            }
            .article-tab {
              font-size: 14px;
              padding: 3px 0;
              min-height: 22px;
              min-width: 70px;
              color: #ffffff;
            }
            .article-tab-kg, .article-tab-zt {
              background-color: #f54435;
            }
            .article-tab-qyw {
              background-color: #b2c92b;
            }
            .article-tab-zc {
              background-color: #2acfbc;
            }
            .article-tab-cd {
              background-color: #f6bc2a;
            }
            .article-tab-sq, .article-tab-wdk, .article-tab-jbsq {
              background-color: #20a2ff;
            }
            .article-tab-sq {
              min-width: 120px;
              padding: 3px 10px;
            }
            .article-tab-jbsq {
              min-width: 90px;
              padding: 3px 10px;
            }
          }
          p:nth-child(1) {
            span:nth-child(1) {
              width: auto;
              text-align: left;
              margin-right: 10px;
            }
          }
          img {
            height: 15px;
            margin-right: 5px;
          }
        }
        .signIn-article-bottom {
          position: absolute;
          top: 122px;
        }
      }
    }
    .Punch-btn-wrapper {
      position: absolute;
      top: 300px;
      box-sizing: border-box;
      width: 100%;
      .Punch-btn-bg {
        margin: 0 auto;
        width: 250px;
        height: 250px;
        img {
          width: 100%;
          height: 100%;
        }
      }
      .Punch-btn-btn {
        position: absolute;
        top: 50%;
        left: 50%;
        margin: -80px 0 0 -80px;
        padding-top: 80px;
        box-sizing: border-box;
        width: 160px;
        height: 160px;
        border-radius: 50%;
        background-color: rgba(255, 255, 255, 0);
        p {
          margin-bottom: 8px;
          width: 100%;
          height: 16px;
          line-height: 16px;
          color: #ffffff;
          font-size: 16px;
          text-align: center;
        }
        p:nth-child(2) {
          margin-bottom: 40px;
        }
      }
    }
    .getLocation-alert-wrapper {
      width: 276px;
      background-color: #ffffff;
      border-radius: 4px;
      padding-bottom: 18px;
      .getLocation-alert-content {
        box-sizing: border-box;
        padding: 20px 55px 0;
        width: 100%;
        text-align: center;
        p {
          width: 100%;
          line-height: 25px;
          font-size: 15px;
        }
      }
      .getLocation-alert-btnBox {
        overflow: hidden;
        padding: 25px 22px 0;
        .getLocation-alert-btn {
          box-sizing: border-box;
          width: 100px;
          height: 40px;
          line-height: 40px;
          border-radius: 4px;
          font-size: 14px;
          text-align: center;
        }
        .getLocation-alert-btn:nth-child(1) {
          border: 1px solid #bfcbd8;
          background-color: #ffffff;
          color: #1f2d3d;
        }
        .getLocation-alert-btn:nth-child(2) {
          background-color: #20a2ff;
          color: #ffffff;
        }
        .getLocation-alert-btnLeft {
          float: left;
        }
        .getLocation-alert-btnCenter {
          margin: 0 auto;
        }
        .getLocation-alert-btnRight {
          float: right;
        }
        .getLocation-alert-btnColor {
          border: 1px solid #20a2ff;
          background-color: #20a2ff;
          color: #ffffff;
        }
      }
    }
    .punch-success-wrapper {
      width: 250px;
      border-radius: 4px;
      .punch-success-box {
        box-sizing: border-box;
        width: 100%;
        padding: 10px;
        .punch-success-header {
          padding: 10px 80px 17px;
          overflow: hidden;
          .punch-success-headerLeft {
            float: left;
            width: 60px;
            height: 0;
            margin-left: -60px;
            margin-top: 8px;
            border-top: 1px solid #cbd5df;
          }
          .punch-success-headerCenter {
            float: left;
            width: 100%;
            text-align: center;
            font-size: 16px;
            line-height: 16px;
            color: #99a9bf;
          }
          .punch-success-headerRight {
            float: right;
            width: 60px;
            height: 0;
            margin-right: -60px;
            margin-top: 8px;
            border-top: 1px solid #cbd5df;
          }
        }
        .punch-success-content {
          width: 100%;
          .punch-success-img {
            width: 100%;
            padding-bottom: 15px;
            img {
              margin: 0 auto;
              width: 50px;
              height: 50px;
              border-radius: 50%;
            }
          }
          .punch-success-time {
            font-size: 16px;
            height: 16px;
            line-height: 16px;
            color: #1f2d3d;
            padding-bottom: 10px;
          }
          .punch-success-tab {
            .punch-success-tabHeight {
              height: 22px;
              font-size: 12px;
              color: #ffffff;
            }
            .article-tab-kg, .article-tab-zt {
              background-color: #f54435;
            }
            .article-tab-qyw {
              background-color: #b2c92b;
            }
            .article-tab-zc {
              background-color: #2acfbc;
            }
            .article-tab-cd {
              background-color: #f6bc2a;
            }
            .article-tab-sq, .article-tab-wdk {
              background-color: #20a2ff;
            }
          }
        }
        .punch-success-btn {
          margin-top: 30px;
          width: 100%;
          height: 40px;
          line-height: 40px;
          border-radius: 4px;
          color: #ffffff;
          background-color: #20a2ff;
        }
      }
    }
  }
</style>



